import React from 'react';
import {ButtonComponent} from './../../../Components/Button/index.styles';
import {Loading} from './../../../Icons/Loading';

export const Form = ({
  handleChange,
  handleSubmit,
  values,
  errors,
  touched,
  isSubmitting,
}) => {
  return (
    <form onSubmit={handleSubmit} className='contactForm'>
      <div className='form-group'>
        <input
          id='name'
          type='text'
          name='name'
          value={values.name}
          placeholder='Nombre'
          onChange={handleChange}
          className='form-control input-text'
        />
        {touched.name && errors.name && (
          <p className='helper-text'>{errors.name}</p>
        )}
      </div>
      <div className='form-group'>
        <input
          id='email'
          type='text'
          name='email'
          value={values.email}
          placeholder='Email'
          onChange={handleChange}
          className='form-control input-text'
        />
        {touched.email && errors.email && (
          <p className='helper-text'>{errors.email}</p>
        )}
      </div>
      <div className='form-group'>
        <input
          id='telefono'
          type='text'
          name='telefono'
          value={values.telefono}
          placeholder='Telfono'
          onChange={handleChange}
          className='form-control input-text'
        />
        {touched.telefono && errors.telefono && (
          <p className='helper-text'>{errors.telefono}</p>
        )}
      </div>
      <div className='form-group'>
        <textarea
          id='message'
          rows='5'
          name='message'
          placeholder='Mensaje'
          value={values.message}
          onChange={handleChange}
          className='form-control input-text text-area'
        ></textarea>
        {touched.message && errors.message && (
          <p className='helper-text'>{errors.message}</p>
        )}
      </div>
      <div className='text-center'>
        <ButtonComponent
          type='submit'
          disabled={isSubmitting}
          className='btn-get-started'
        >
          {!isSubmitting ? 'Enviar' : <Loading className='girar' />}
        </ButtonComponent>
      </div>
    </form>
  );
};
