import React, {useState, useEffect, useRef} from 'react';
import AliceCarousel from 'react-alice-carousel';
import 'react-alice-carousel/lib/alice-carousel.css';

import {Filters} from './Components/Filters';
import {ButtonModal} from './Components/ButtonModal';
import {PortfolioSection, ButtonArrow} from './index.styles';
import {ArrowLeft} from './../../Icons/ArrowLeft';
import {ArrowRight} from './../../Icons/ArrowRight';

export const Portfolio = () => {
  const [items, setItems] = useState([]);
  const [active, setActive] = useState('todo');
  const ref = useRef(AliceCarousel);

  useEffect(() => {
    fetch('./rest/portfolio')
      .then((res) => res.json())
      .then((result) => {
        // ordeno los objetos por el numero de orden
        setItems(result.sort((a, b) => a.numOrder - b.numOrder));
      });
  }, []);

  const responsive = {
    0: {items: 1},
    767: {items: 2},
    991: {items: 3},
  };

  const arregloImagenes = [];

  items.map((item, key) => {
    if (
      active.toUpperCase() === 'TODO' ||
      active.toUpperCase() === item.categoria.toUpperCase()
    ) {
      arregloImagenes.push(
        <div className='portfolio-item' key={key}>
          <div className='portfolio-wrap'>
            <img src={item.url} alt={item.titulo} className='img-fluid' />
            <div className='portfolio-info'>
              <h4>{item.titulo}</h4>
              <ButtonModal
                idPortfolio={item.id}
                titulo={`${item.categoria.toUpperCase()} ${item.titulo.toUpperCase()}`}
              />
            </div>
          </div>
        </div>
      );
    }
    return true;
  });

  const onClick = (categoria) => {
    setActive(categoria);
  };

  return (
    <PortfolioSection id='portfolio'>
      <div className='container'>
        <div className='section-title' data-aos='fade-up'>
          <h2>Portfolio</h2>
          <p>Algunos de nuestros proyectos realizados</p>
        </div>

        <Filters active={active} onClick={onClick} />

        <div
          className='row'
          data-aos='fade-up'
          data-aos-delay='200'
          align='center'
        >
          <AliceCarousel
            autoPlay={false}
            infinite={true}
            dotsDisabled={true}
            buttonsDisabled={true}
            items={arregloImagenes}
            responsive={responsive}
            stopAutoPlayOnHover={false}
            mouseTrackingEnabled={true}
            ref={ref}
          />
          <ButtonArrow onClick={() => ref.current.slidePrev()}>
            <ArrowLeft />
          </ButtonArrow>
          <ButtonArrow onClick={() => ref.current.slideNext()}>
            <ArrowRight />
          </ButtonArrow>
        </div>
      </div>
    </PortfolioSection>
  );
};
