import styled, {css} from 'styled-components';

export const BackToTopComponent = styled.a`
  ${({type}) => {
    const _transition = `transition:all 0.3s ease-in-out;
      -moz-transition:all 0.3s ease-in-out;
      -webkit-transition:all 0.3s ease-in-out;`;

    return css`
      z-index: 21;
      position: fixed;
      ${type === 'bottom' ? `right: 0` : `left: 12px`};
      bottom: ${type === 'bottom' ? `0` : `12px`};

      display: flex;
      flex-direction: column;
      align-items: center;
      cursor: pointer;

      i {
        text-align: center;
        ${type === 'bottom'
          ? `background-color: #fff; width:100px; border-radius: 40px 40px 0 0;`
          : ``};
        :hover {
          ${type === 'bottom'
            ? `background-color: #8E6BB1;`
            : `svg {height: 85px; ${_transition}}`};

          ${_transition}
        }
      }
    `;
  }}
`;
