import React from 'react';
import {Link} from 'react-scroll';

export const AnchorMenu = ({sectionName, body}) => {
  return (
    <Link
      activeClass='active'
      to={sectionName}
      offset={-80}
      spy={true}
      smooth={true}
      duration={1500}
      className={sectionName}
    >
      {body ? body :sectionName}
    </Link>
  );
};
