import React from 'react';
import {MsjSection} from './index.styles';

export const Msj = ({imagenStr, text, textSize, bgImgPosition = 'bottom'}) => {
  return (
    <MsjSection
      id='msj'
      className='msj'
      imagenStr={imagenStr}
      textSize={textSize}
      bgImgPosition={bgImgPosition}
    >
      <div className='container'>
        <div className='row'>
          {textSize === 'small' ? (
            <p>{text}</p>
          ) : (
            <h2>
              <b>{text}</b>
            </h2>
          )}
        </div>
      </div>
    </MsjSection>
  );
};
