import React from 'react';
import {scroller} from 'react-scroll';

import {ItemList, WrapperList} from './../index.styles';

export const List = ({closeModal}) => {

  const onClick = (name) => {
    closeModal();
    const scrollType = {
      duration: 1500,
      delay: 50,
      smooth: true,
      offset: -80,
    };
    scroller.scrollTo(name, scrollType);
  };

  return (
    <WrapperList>
      <li>
        <ItemList onClick={() => onClick('servicios')}>
          SERVICIOS
        </ItemList>
      </li>
      <li>
        <ItemList onClick={() => onClick('trabajos')}>
          TRABAJOS
        </ItemList>
      </li>
      <li>
        <ItemList onClick={() => onClick('portfolio')}>
          PORTFOLIO
        </ItemList>
      </li>
      <li>
        <ItemList onClick={() => onClick('clientes')}>
          CLIENTES
        </ItemList>
      </li>
      <li>
        <ItemList onClick={() => onClick('equipo')}>
          EQUIPO
        </ItemList>
      </li>
      <li>
      <ItemList onClick={() => onClick('contacto')}>
          CONTACTO
        </ItemList>
      </li>
    </WrapperList>
  );
};
