import React, {useState, useEffect} from 'react';
import Modal from 'react-modal';

import {ContactSection, WrapperModalContent} from './index.styles';
import {Form} from './Form';
import {Telefono} from './../../Icons/Telefono';
import {Mail} from './../../Icons/Mail';
import {MsjSend} from './../../Icons/MsjSend';
import {SocialLinks} from './SocialLinks';
import {Formik} from 'formik';
import {WrapperButton} from './../../Components/WrapperButton/index.styles';
import {Close} from './../../Icons/Close';
import {validation} from './Utils/validation';

export const Contact = () => {
  const [visible, setVisible] = useState(false);

  useEffect(() => {
    Modal.setAppElement('body');
  }, []);

  const customStyles = {
    content: {
      top: '0',
      left: '0',
      right: '0',
      border: '0',
      bottom: '0',
      borderRadius: '0',
      backgroundColor: 'rgba(21, 21, 21, 0.95)',
    },
  };

  const handleSubmit = (values, actions) => {
    const searchParams = Object.keys(values)
      .map((key) => {
        return encodeURIComponent(key) + '=' + encodeURIComponent(values[key]);
      })
      .join('&');
    
    window.grecaptcha.ready(() => {
      window.grecaptcha
        .execute('6LfA__4ZAAAAAJtOZ9Ghtkq2IA7QIBCIspy7c-WU', {action: 'submit'})
        .then((token) => {
          const requestOptions = {
            method: 'POST',
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8',
            },
            body: searchParams+`&token=${token}`,
          };

          fetch('./rest/send_email/', requestOptions)
            .then(() => {
              setVisible(true);
              actions.setSubmitting(false);
              actions.resetForm();
            })
            .catch((error) => {
              console.error(error);
              setVisible(true);
              actions.setSubmitting(false);
              actions.resetForm();
            });
        });
    });
  };

  const closeModal = () => {
    setVisible(false);
  };

  return (
    <ContactSection className='container'>
      <section className='main-section contact' id='contact'>
        <div className='row'>
          <div className='col-lg-6 col-sm-12 wow fadeInLeft'>
            <div className='contact-info-box phone clearfix'>
              <h3>
                <Telefono width='22px' />
                Telfono
              </h3>
              <span>362 474-5337</span>
            </div>
            <div className='contact-info-box email clearfix'>
              <h3>
                <Mail width='26px' />
                E-mail
              </h3>
              <span>tokioagencia034@gmail.com</span>
            </div>
            <SocialLinks />
          </div>
          <div className='col-lg-6 col-sm-12 wow fadeInUp delay-05s'>
            <div className='form'>
              <Formik
                initialValues={{
                  name: '',
                  email: '',
                  telefono: '',
                  message: '',
                }}
                onSubmit={handleSubmit}
                validate={validation}
              >
                {(props) => <Form {...props} />}
              </Formik>
              <Modal
                isOpen={visible}
                onRequestClose={closeModal}
                style={customStyles}
                contentLabel='Example Modal'
              >
                <WrapperButton onClick={closeModal}>
                  <Close color='white' />
                </WrapperButton>
                <WrapperModalContent>
                  <MsjSend />
                  <h3>Su mensaje ha sido enviado con xito!</h3>
                  <h4>En breve recibir nuestra respuesta,</h4>
                  <h4>muchas gracias =)</h4>
                </WrapperModalContent>
              </Modal>
            </div>
          </div>
        </div>
      </section>
    </ContactSection>
  );
};
