import React, {useEffect, useState} from 'react';
import Modal from 'react-modal';

import {List} from './List';
import {AnchorMenu} from './AnchorMenu';
import {Close} from './../../../Icons/Close';
import {MenuHamburguer} from './../../..//Icons/MenuHamburguer';
import {WrapperLogo} from './../index.styles';
import {WrapperButton} from './../../../Components/WrapperButton/index.styles';

export const NavBarMobile = () => {
  const [visible, setVisible] = useState(false);

  useEffect(() => {
    Modal.setAppElement('nav')
  }, [])

  const openModal = () => {
    setVisible(true);
  };

  const closeModal = () => {
    setVisible(false);
  };

  const customStyles = {
    content: {
      top: '0',
      right: '0',
      bottom: '0',
      left: '15%',
      borderRadius: '0',
    },
  };

  return (
    <>
      <nav className='mobile-nav d-sm-block d-lg-none'>
        <WrapperButton onClick={openModal}>
          <MenuHamburguer />
        </WrapperButton>
        <WrapperLogo>
          <AnchorMenu
            sectionName='servicios'
            body={<img src='img/logo-tokio.svg' alt='Tokio Agencia Logo' />}
          />
        </WrapperLogo>
        <Modal
          isOpen={visible}
          onRequestClose={closeModal}
          style={customStyles}
          contentLabel='Example Modal'
        >
          <WrapperButton onClick={closeModal}>
            <Close />
          </WrapperButton>
          <List closeModal={closeModal} />
        </Modal>
      </nav>
    </>
  );
};
