import React, {useEffect, useState} from 'react';

import {ButtonFilter} from './../index.styles';

export const Filters = ({active, onClick}) => {
  const [categorias, setCategorias] = useState([]);

  useEffect(() => {
    fetch('./rest/categorias')
      .then((res) => res.json())
      .then((result) => {
        // ordeno los objetos por el numero de orden
        setCategorias(result);
      });
  }, []);

  return (
    <div className='row' data-aos='fade-up' data-aos-delay='100'>
      <div className='col-lg-12 d-flex justify-content-center'>
        <ul id='portfolio-flters'>
          <li>
            <ButtonFilter
              active={active.toUpperCase() === 'TODO'}
              onClick={() => onClick('todo')}
            >
              Todo
            </ButtonFilter>
          </li>
          {categorias.map((item, key) => (
            <li key={key}>
              <ButtonFilter
                active={active.toUpperCase() === item.categoria.toUpperCase()}
                key={key}
                onClick={() => onClick(item.categoria)}
              >
                {item.categoria}
              </ButtonFilter>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
};
