import React from 'react';
import {scroller} from 'react-scroll';

import {ButtonComponent} from './../../Components/Button/index.styles';
import {HeroSection} from './index.styles';

export const Hero = () => {

  const onClick = (name) => {
    const scrollType = {
      duration: 1500,
      delay: 50,
      smooth: true,
      offset: -80,
    };
    scroller.scrollTo(name, scrollType);
  };

  return (
    <HeroSection id='hero'>
      <div className='hero-container'>
        <a href='index.html' className='hero-logo' data-aos='zoom-in'>
          <img src='img/logo-tokio.svg' alt='' />
        </a>
        <h1 data-aos='zoom-in'>CREAMOS EXPERIENCIAS DIGITALES</h1>
        <h2 data-aos='fade-up'>Conectamos personas con marcas</h2>
        <ButtonComponent
          onClick={() => onClick('services')}
          className='btn-get-started'
        >
          CONOCENOS
        </ButtonComponent>
      </div>
    </HeroSection>
  );
};
