import React, {useEffect, useState} from 'react';

export const ArrayImages = () => {
  const [items, setItems] = useState([]);

  useEffect(() => {
    
    fetch('./rest/clients')
      .then((res) => res.json())
      .then((result) => {
        // ordeno los objetos por el numero de orden
        setItems(result.sort((a, b) => a.numOrder - b.numOrder));
      });
  }, []);

  const handleOnDragStart = (e) => e.preventDefault();

  let arregloImagenes = [];
  let key;
  for(let index = 0; index < items.length;) {
    index = index+3;
    key = index >= items.length ? items.length : index;
    arregloImagenes.push(
      <>
        <img
          onDragStart={handleOnDragStart}
          src={items[key-3].url}
          alt={items[key-3].description}
        />
        <img
          onDragStart={handleOnDragStart}
          src={items[key-2].url}
          alt={items[key-2].description}
        />
        <img
          onDragStart={handleOnDragStart}
          src={items[key-1].url}
          alt={items[key-1].description}
        />
      </>
    );
  }

  return arregloImagenes;
};
