export const validation = (values) => {
  let errors = {};

  if (!values.name) {
    errors.name = 'Completa tu nombre.';
  } else if (values.name.length <= 2) {
    errors.name = 'Ingres ms de 2 caracteres.';
  }

  if (!values.telefono && !values.email) {
    errors.telefono = 'Completa mnimo un campo de contacto.';
    errors.email = 'Completa mnimo un campo de contacto.';
  } else {
    if (
      values.email &&
      !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)
    ) {
      errors.email = 'El email ingresado es invlido, verifcalo.';
    }
    if (values.telefono && values.telefono.length < 6) {
      errors.telefono = 'Ingres ms de 5 caracteres.';
    }
  }

  if (!values.message) {
    errors.message = 'Escrbenos un mensaje para poder atender tu consulta.';
  }

  return errors;
};
