import React, {useEffect, useState} from 'react';
import Gallery from 'react-photo-gallery';
import {useMediaQuery} from 'react-responsive';

export const GalleryComponent = ({idPortfolio}) => {
  const [photos, setPhotos] = useState([]);
  
  useEffect(() => {
    let mounted = true;
    
    fetch(`./rest/portfolio_detalle?fk_images_portfolio=${idPortfolio}`)
      .then((res) => res.json())
      .then((result) => {
        // ordeno los objetos por el numero de orden
        if (mounted) {
          result ? setPhotos(result
            .sort((a, b) => a.numOrder - b.numOrder)
            .map((photo, key) => ({
                src: photo.url,
                width: 1,
                height: 1,
                key: key.toString(),
              })
            )) : setPhotos([]);
        }
      });

      return () => mounted = false;
  }, [idPortfolio]);

  const isMobile = useMediaQuery({query: '(max-width: 768px)'});
  const isTablet = useMediaQuery({query: '(max-width: 990px)'});

  return (
    <div className='container'>
      <div className='row'>
        <div className='col-12'>
          {photos.length > 0 ? (
            <Gallery
              photos={photos}
              direction='column'
              columns={isMobile ? 1 : isTablet ? 2 : 3}
            />
          ) : null}
        </div>
      </div>
    </div>
  );
};
