import React, {useEffect, useState} from 'react';
import Modal from 'react-modal';

import {Close} from './../../../Icons/Close';
import {WrapperButton} from './../../../Components/WrapperButton/index.styles';
import {WrapperModalContent} from './Components.styles';
import {GalleryComponent} from './Gallery';

export const ButtonModal = ({idPortfolio, titulo}) => {
  const [visible, setVisible] = useState(false);

  useEffect(() => {
    Modal.setAppElement('body');
  }, [])

  const openModal = () => {
    setVisible(true);
  };

  const closeModal = () => {
    setVisible(false);
  };

  const customStyles = {
    content: {
      top: '0',
      left: '0',
      right: '0',
      border: '0',
      bottom: '0',
      borderRadius: '0',
      backgroundColor: 'rgba(21, 21, 21, 0.95)',
    },
  };
  return (
    <>
      <button onClick={openModal} />
      <Modal
        isOpen={visible}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel='Example Modal'
      >
        <WrapperButton onClick={closeModal}>
          <Close color='white' />
        </WrapperButton>
        <WrapperModalContent>
          <h3>{titulo}</h3>
          <GalleryComponent idPortfolio={idPortfolio} />
        </WrapperModalContent>
      </Modal>
    </>
  );
};
