import React, {useState, useEffect} from 'react';
import {scroller} from 'react-scroll';

import {BackToTopComponent} from './index.styles';
import {ArrowUp} from './../../Icons/ArrowUp';
import {WhatsApp} from './../../Icons/WhatsApp';

export const BackToTop = () => {
  const [visible, setVisible] = useState(false);

  useEffect(() => {
    document.addEventListener('scroll', function () {
      setVisible(window.pageYOffset > 300);
    });
  }, []);

  const onClick = (name) => {
    const scrollType = {
      duration: 1500,
      delay: 50,
      smooth: true,
      offset: -80,
    };
    scroller.scrollTo(name, scrollType);
  };

  return (
    <>
      {visible && (
        <>
          <BackToTopComponent
            href='https://wa.me/543624745337'
            target='_blank'
            rel='noreferrer'
            className='back-to-top'
          >
            <WhatsApp height='80px' className='wp' />
          </BackToTopComponent>
          <BackToTopComponent
            type='bottom'
            className='back-to-top'
            onClick={() => onClick('inicio')}
          >
            <ArrowUp height='40px' />
          </BackToTopComponent>
        </>
      )}
    </>
  );
};
